import React, { Component } from 'react'
import styles from './Footer.module.css'
import { NavLink } from 'react-router-dom'



const Footer = () => {
    return (
        <div className={styles.FooterWrapper}>
            <div className={styles.firstFooter}>
                <div className = {styles.firstLine}>
                    <div className = {styles.firstLineList}>
                    <div> </div>
                    <div> </div>
                    <div> </div>
                    <div></div>
                    <div></div>
                    <div></div>
                    <div>  </div>
                    <div></div>
                    </div>
                </div>
                <div className = {styles.line}></div>
                <div className = {styles.secondLine}>
                <div className = {styles.secondLineList}>
                    <div></div>

                    <div className ={styles.navigationLink}><NavLink to='/catalog/catalog43'>  1</NavLink></div>
                    <div className ={styles.navigationLink}><NavLink to='/catalog/catalog66'>  2</NavLink></div>
                    <div className ={styles.navigationLink}><NavLink to='/catalog/catalog95'>  3</NavLink></div>
                    <div className ={styles.navigationLink}><NavLink to='/catalog/catalogsoviets'>  4</NavLink></div>
                    </div>
                </div>

            </div>

            <div className={styles.secondFooter}>
                <div className={styles.infoNav}>
                    <div className={styles.greyText}>
                        Copyright  - Perekupi.ru 2019.   
                        </div>
                </div>
            </div>
        </div>
    )
}

export default Footer